/* Main Colors */
/* Gray */
.W1Vj4 .uui-thumb-vertical, .W1Vj4 .uui-thumb-horizontal {
  background: #CED0DB;
  border-radius: 1.5px;
}
.W1Vj4 .uui-thumb-vertical:hover, .W1Vj4 .uui-thumb-horizontal:hover {
  border-radius: 3px;
}
.W1Vj4 .uui-thumb-vertical:active, .W1Vj4 .uui-thumb-horizontal:active {
  border-radius: 3px;
  background: #6C6F80;
}
.W1Vj4 .uui-thumb-vertical {
  width: 3px;
  margin-left: 3px;
}
.W1Vj4 .uui-thumb-vertical:hover {
  width: 6px;
  margin-left: 0;
}
.W1Vj4 .uui-thumb-vertical:active {
  width: 6px;
  margin-left: 0;
}
.W1Vj4 .uui-thumb-horizontal {
  height: 3px;
  margin-top: 3px;
}
.W1Vj4 .uui-thumb-horizontal:hover {
  height: 6px;
  margin-top: 0;
}
.W1Vj4 .uui-thumb-horizontal:active {
  height: 6px;
  margin-top: 0;
}