._17gqq.uui-slider-container {
  display: flex;
  position: relative;
  align-items: center;
  width: 100%;
  padding: 12px 0;
  cursor: pointer;
}
._17gqq.uui-slider-container .uui-slider-handle {
  position: absolute;
  left: 0;
}
._17gqq.uui-slider-container .uui-slider-handle:focus {
  outline-offset: 1px;
  outline-color: var(--uui-color-700);
}
._17gqq.uui-slider-container .uui-slider {
  pointer-events: none;
}
._17gqq.uui-slider-container .uui-slider-filled {
  position: absolute;
  pointer-events: none;
}
._17gqq.uui-slider-container .uui-slider-scale {
  position: absolute;
}
._17gqq.uui-slider-container .uui-slider-scale-number {
  position: absolute;
}
._17gqq.uui-slider-container .uui-slider-scale-dot {
  position: absolute;
}