.zx5I1.uui-shadow-top::before, .zx5I1.uui-shadow-bottom::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  height: 6px;
  opacity: 0;
  transition: opacity 200ms;
}
.zx5I1.uui-shadow-top::before {
  top: 0;
  background: linear-gradient(to bottom, black 0%, rgba(0, 0, 0, 0) 100%);
  z-index: 1;
}
.zx5I1.uui-shadow-bottom::after {
  bottom: 0;
  background: linear-gradient(to top, black 0%, rgba(0, 0, 0, 0) 100%);
}
.zx5I1.uui-shadow-top-visible::before {
  opacity: 1;
}
.zx5I1.uui-shadow-bottom-visible::after {
  opacity: 1;
}
.zx5I1 .uui-track-horizontal,
.zx5I1 .uui-track-vertical {
  position: absolute;
  border-radius: 3px;
  z-index: 6;
}
.zx5I1 .uui-track-horizontal {
  height: 6px;
  right: 2px;
  bottom: 2px;
  left: 2px;
}
.zx5I1 .uui-track-vertical {
  width: 6px;
  right: 2px;
  top: 2px;
  bottom: 2px;
}