/* Main Colors */
/* Gray */
.mWXar {
  --background-color-hover: #1D1E26;
  --icon-fill: #FFFFFF;
  --menu-background-color: #1D1E26;
  --menu-background-color-hover: #0079b5;
  --items-background-color: #1D1E26;
  position: relative;
  display: flex;
}
.mWXar.uui-burger-menu-open {
  z-index: 1001;
}
.mWXar.uui-burger-menu-open ~ #global_menu_toggle {
  background-color: var(--menu-background-color);
  z-index: 1001;
}
.mWXar.uui-burger-menu-open ~ #global_menu_toggle :hover {
  background-color: var(--menu-background-color-hover);
}
.mWXar .uui-burger-button {
  width: 60px;
  cursor: pointer;
  fill: var(--icon-fill);
}
.mWXar .uui-burger-button:hover {
  background-color: var(--background-color-hover);
}
.mWXar .uui-burger-button:active {
  background-color: var(--background-color-hover);
}
.mWXar .uui-burger-button .uui-icon svg {
  width: 60px;
  height: 60px;
}
.mWXar .uui-burger-logo-container {
  position: absolute;
  left: 60px;
  top: 0;
}
.mWXar .uui-burger-logo {
  display: block;
  height: 60px;
}

._3YsmN.uui-burger-overlay {
  opacity: 0;
}
._3YsmN.uui-burger-overlay.uui-burger-overlay-visible {
  opacity: 1;
  position: fixed;
  z-index: 1000;
  top: 60px;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(29, 30, 38, 0.8);
  transition: opacity 0.2s ease-in-out;
}
._3YsmN .uui-burger-items {
  --background-color-hover: #1D1E26;
  --icon-fill: #FFFFFF;
  --menu-background-color: #1D1E26;
  --menu-background-color-hover: #0079b5;
  --items-background-color: #1D1E26;
  position: relative;
  display: flex;
  flex-direction: column;
  top: 0;
  width: 300px;
  background: var(--items-background-color);
  transform: translateX(-100px);
  overflow-y: overlay;
}
._3YsmN .uui-burger-items.uui-burger-items-visible {
  transform: translateX(0);
}