.O2Lax ol, .O2Lax ul {
  padding: 0;
}
.O2Lax li {
  margin-left: 1.25em;
  line-height: 1.5;
}
.O2Lax pre {
  padding: 1.25em;
  color: #F5F6FA;
  background-color: #474A59;
  font-family: monospace;
  white-space: pre-wrap;
  word-wrap: normal;
}
.O2Lax img {
  max-width: 100%;
}
.O2Lax figure {
  margin: 0;
}
.O2Lax figure figcaption {
  font-size: 0.9em;
  opacity: 0.9;
  text-align: center;
}
.O2Lax p {
  margin: 0.5em 0;
  line-height: 1.5;
}

