.uui-error-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 36px 30px;
}

.uui-error-image {
  flex: 1 1 300px;
  width: 640px;
  max-width: 100%;
}

.uui-error-title {
  font-family: "Museo Sans", "Sans Semibold", Arial, sans-serif;
  font-size: 30px;
  line-height: 36px;
  text-align: center;
  margin-top: 60px;
  color: #303240;
  max-width: 700px;
}

.uui-error-subtitle {
  font-family: "Sans Regular", Arial, sans-serif;
  text-align: center;
  line-height: 24px;
  font-size: 16px;
  color: #303240;
  max-width: 700px;
}

@media screen and (max-width: 720px) {
  .uui-error-image {
    flex: 1 1 200px;
    width: 400px;
  }

  .uui-error-title {
    font-family: "Sans Regular", Arial, sans-serif;
    font-size: 24px;
    line-height: 30px;
    margin-top: 48px;
    max-width: 460px;
  }

  .uui-error-subtitle {
    font-family: "Sans Regular", Arial, sans-serif;
    text-align: center;
    line-height: 24px;
    font-size: 16px;
    color: #303240;
    max-width: 460px;
  }
}